

  <script>
    import {push, pop, replace} from 'svelte-spa-router'
    import { fade, fly, slide, scale, blur } from "svelte/transition"
    import * as eases from "svelte/easing"
    import Loading from './Loading.svelte'
    import Player from './Player.svelte'
    import Recomendation from './Recomendation.svelte'
    import { onMount } from 'svelte';
    

    let currentEasing = eases.circIn
    let duration = 700
    
    export let params = {}
    let genres = []
  
    // let nowPage = params.id
    // $: if (nowPage !== params.id) { //watch the params.id for changes
    //     document.location.reload(); //invoke your method to reload data
    // }

    //let videos = []

    let data


    data = {
        files: [],
        recomend: null,
        loading : true
    }

    let qualities = new Array

    let loadDataFilm = (id) => {
        fetch("https://api.themoviedb.org/3/movie/"+id+"?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU")
            .then(response => response.json())
                .then((function(result) {
                    data.recomend = result.id
                    data.overview = result.overview
                    data.poster_path = (result.poster_path !== null && result.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+result.poster_path : '/build/noposter.png'
                    data.backdrop_path = (result.backdrop_path !== null && result.backdrop_path !== undefined)? 'https://image.tmdb.org/t/p/original'+result.backdrop_path : './build/backdrop.webp'
                    data.tagline = result.tagline
                    data.runtime = (result.runtime / 60 | 0) + "  " + result.runtime % 60 + " "
                    genres = result.genres
                })
        )}

    function module(page) {
            data.loading = true
            fetch('https://38.svetacdn.in/api/movies?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O&imdb_id='+page)
                .then(response => response.json())
                .then((function(result) {
                    data = result.data[0]
                    loadDataFilm(params.id)
                    document.title = ': '+data.ru_title
                    data.media.forEach(x => {

                        let arr = new Array
                        x.qualities.forEach(v => {
                           arr.push(v.resolution)
                        })
                        qualities.push(arr)
                    })
                    data.loading = false
                    console.log(data)
                })
            )
    }
    module(params.id)
    let filmDate = (date,id) => {
        let data1 = new Date(date)
        if(id === 1) {data1 = data1.getFullYear(data1)}
        if(id === 2) {
            let year = data1.getFullYear(data1)
            let month = data1.getMonth(data1)+1
            let day = data1.getDay(data1)
            data1 = day+'/'+month+'/'+year
        }
        return data1
    }
</script>
{#if data.loading === true}
    <Loading />
{:else if data.loading !== true}
<div in:fade={{easing: currentEasing, duration}}>
    
    <header class="shadow-inner">

        <!-- Background image -->
        <div
          class="bg-image"
          style="
            background-image: url(
                {data.backdrop_path});
            height: 500px
          "
            >
          <div class="mask" style="background:linear-gradient(
            45deg,
            rgba(79, 74, 58, 0.7),
            rgb(4%, 16%, 40%, 0.7) 100%
          )">

            <div class="d-flex h-100 container">
                <div class="d-flex py-3 h-100">
                    <img style="border-radius: 8px;" class="h-100 shadow-3 position-relative" src={data.poster_path} alt="Poster">
                    {#each data.media as {source_quality, max_quality},i}
                    {#if i===0}
                        <span class="badge p-2 bg-warning bg-gradient shadow-2 position-absolute m-2 text-uppercase">{source_quality+' '+max_quality}</span>
                    {/if}
                {/each}
                </div>

                <div class="d-flex flex-column w-100 text-white m-3">
                    <div class="d-flex">
                        <h2 >{data.ru_title}</h2>
                        <h2 class="fw-light"><span class="">&nbsp;({filmDate(data.year, 1)})</span></h2>
                    </div>
                    <div class="d-flex" style="

                        border-radius: 2px;
                        margin-left: 7px;
                        margin-right: 7px;">
                        <!-- <span class="border" style="

                            border-radius: 2px;
                            margin-right: 7px;">
                          R
                        </span> -->
                        <span class="p-1">
                            {filmDate(data.year, 2)}
                        </span>
                            {#each genres  as {id,name}, i}
                                <span class="genre borders p-1">{name}</span>
                            {/each}

                        <span class="time p-1">
                            {#if data.runtime !== undefined}
                                {data.runtime}
                            {/if}
                        </span>
                    
                    </div>


                    <div>
                        {#if data.overview !== undefined}
                            <h3 style=" 
                                font-size: 1.1em;
                                font-weight: 400;
                                font-style: italic;
                                opacity: 0.7;" class="text-start fst-italic">
                                {data.tagline}</h3>
                        {/if}
                        <h3 class="text-start"></h3>
                        {#if data.overview !== undefined}
                            <p class="text-start">
                                {data.overview}</p>
                        {/if}
                    </div>
              </div>
            </div>
          </div>
        </div>
      </header>
      <main class="container mt-5 mb-5">
        {#each data.media as {path}, i}
            {#if i === 0}
                <Player videos = {path} poster = {data.backdrop_path} {qualities} />
            {/if}
        {/each}
        {#if data.recomend !== undefined}
            <Recomendation url = {data.recomend}/>
        {/if}
      </main>
</div>
{/if}