<script>
    import { Player, Video, DefaultUi} from '@vime/svelte';
    import { fade, fly, slide, scale, blur } from "svelte/transition"
    import Loading from './Loading.svelte'

    export let videos
    export let poster
    export let qualities

    let src = []
    let translations
    let loading = true
    let selected = 0
    let resolution

    let module = () => {
            fetch('https://38.svetacdn.in/api/translations?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O')
                .then(response => response.json())
                .then((function(result) {
                    translations = result
                    loadVideos()
                    loading = false
                })
            )}
     module()
    async function loadVideos() {
         try { 
            let res = await fetch('https:'+videos);
            let filmsPage = await res.text();
            if(res.ok) {
                let parser = new DOMParser();
                let doc = parser.parseFromString(filmsPage, 'text/html');
                let files = doc.getElementById("files").value
                let jsonFilm = files.replaceAll('\\', '')
                let filmObject = JSON.parse(jsonFilm);
                
                function translation(id) {
                    let result = translations.data
                    return result.filter(x => x.id == id).map(x => x.short_title)[0]
                }

                const checkurl = () => {
                    Object.entries(filmObject).forEach(element => {
                        let id = element[0]

                        let strValue = element[1]
                        strValue = strValue.replaceAll(' or //', ',')
                        strValue = strValue.split(',')
                        strValue = strValue[13]
                        let str = strValue?.slice(0, -7)
                        src = [...src, {'id': id, 'src': str, 'translation': translation(id)}]
                        src=src
                    });
                    resolution = qualities[selected][0]
                }
                checkurl()
            }
        } catch (error) {
            console.error(':', error);
        }
    }


</script>

{#if loading === true}
    <Loading />
{:else if translations !== undefined}
<div class="card p-2" transition:fade>
    <p> </p>
    <select class='' bind:value={selected} on:change= {()=>resolution = qualities[selected][0]}>
        {#each src as {translation, id}, i}
            <option value={i}>
                <p>{translation}</p>
            </option>
        {/each}
    </select>

        {#each qualities as qualitie, i}
            {#if i==selected}
                <div class="my-3 d-flex justify-content-start">
                    <div class="btn-group">
                        {#each qualitie as res,i}
                                <a  class:active="{resolution === res}"
                                on:click={() => resolution = res} class="btn btn-primary">{res}</a>    
                        {/each}
                    </div>
                </div>
            {/if}
        {/each}

    {#each src as {src}, i}
        {#if i==selected}
                <Player
                    theme="dark"
                    style="--vm-player-theme: #e86c8b;">
                <Video
                    poster="{poster}">
                    <source 
                        data-src="https://{src}{resolution}.mp4" 
                        type="video/mp4"/>
                </Video>
                <DefaultUi />
                </Player>
        {/if}
    {/each}
</div>
{/if}
