<script>
    import {push, pop, replace} from 'svelte-spa-router'
    import { fade, fly, slide, scale, blur } from "svelte/transition";
    import * as eases from "svelte/easing";
    import Loading from './Loading.svelte'
    import {link} from 'svelte-spa-router'
    import { onMount } from 'svelte';

    let currentEasing = eases.circIn
    let duration = 700

    export let url
    let imdbRec = []
    imdbRec.poster_path = null
    let data = []
    $: data.recom = []
    data.loading = true

    async function loadPoster(id,i) {
        if(i.imdb_id !== null) {
            const res = await fetch("https://api.themoviedb.org/3/movie/"+id+"?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU");
            const character = await res.json()
            if(res.ok && i !== undefined) {
                i.genres = character.genres
                // i.genres = (i.genres)? character.genres : ''
                const src = (character.poster_path !== null && character.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
                return src 
            } else {
                if(i !== undefined){
                    const src = (character.poster_path !== null && character.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
                    i.genres = [{id:1,name: ''}]
                    return src 
                }
            }
        } else {
            i.imdb_id = 'id/'+i.id
            const src = (i.poster_path !== null && i.poster_path !== undefined)? 'https://image.tmdb.org/t/p/w500'+character.poster_path : '/build/noposter.png'
            return src
        }
    }
        function loadRecFilm(el,i,arr) {
            fetch("https://api.themoviedb.org/3/movie/"+el.id+"/external_ids?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU")
                .then(response => response.json())
                .then((function(result) {
                    if(result.imdb_id){
                        fetch("https://38.svetacdn.in/api/movies?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O&imdb_id="+result.imdb_id)
                            .then(response => response.json())
                            .then((function(result) {
                                if(result.total == 1) {
                                    result.data[0].vote_average = el.vote_average.toFixed(1)
                                    result.data[0].poster_path = arr[i].poster_path
                                    imdbRec = [...imdbRec, result.data[0]];
                                    // imdbRec.push(result.data[0])
                                    data.recom = imdbRec
                                    console.log(el)
                                } else {
                                //arr.splice(i,1)
                                }
                                //console.log('FilmsRecImdbId: ', result)
                        }))
                    }
                    
                }))

        }


        let loadDataFilm = (id) => {
        data.loading = true
        fetch("https://api.themoviedb.org/3/movie/"+id+"/recommendations?api_key=012107c38dbdef7c24537c12f1f022e6&language=ru-RU&page=1&adult=true")
            .then(response => response.json())
                .then((function(result) { 
                    if(result.total_results) {
                        //imdbRec = result.results
                        result.results.forEach((element, index, array) => loadRecFilm(element,index,array))

                        console.log(result)
                        data.loading=false
                    } else {
                        data.recom = false
                        data.loading = false
                    }
                })
    )}

    loadDataFilm(url)

    let filmDate = (date) => {
        let data1 = new Date(date)
        data1 = data1.getFullYear(data1)
        return data1
    }
</script>
{#if data.loading === true}
<Loading />
{:else if data.loading !== true}

<div class="card text-center mt-3">
