                                {media.source_quality+' '+media.max_quality}
                            </span>
                        {/if}

                    {/each} -->

                <div
                    class="mask"
                    style="
                    background: linear-gradient(
                        45deg,
                        rgba(29, 236, 197, 0.5),
                        rgba(91, 14, 214, 0.5) 100%
                    );
                    "
                >
                <a  use:link={"/film/"+film.imdb_id}>
                    <i class="fas fa-play fa-4x text-primary d-flex align-items-center justify-content-center h-100"></i>
                </a>
            </div>
            </div>
                
            <div class="p-2">
                <a href='' style="
                overflow: hidden;
                max-height: 54px;
                margin: 5px 0 0;
                font-size: 15px;
                font-weight: 600;
                line-height: 18px;
                color: #333;
                ">
                    <h3 style="
                    overflow: hidden;
