                            {film.ru_title}
                        {/if}
                    </h3>
                </a>
                <div class="" style="
                height: 18px;
                margin: 2px 0 3px;
                color: #a6a6a6;
                font-size: 12px;
                font-weight: 400;
                line-height: 18px;
